import React, {Component} from 'react';
import {connect} from 'react-redux';

import {addEffectToCollection, removeEffectFromCollection} from '../actions/collectionsActions';

import Effect from '../components/effect';
import {createEffect, removeEffect, addSoundToEffect, changeEffectVar, moveSound, addSoundReference, removeSoundReference} from '../actions/effectsActions';
import {effectBuilder} from '../actions/effectsConsts';

import {createSound, changeSoundVar, removeSounds} from '../actions/soundsActions';
import {soundBuilder} from '../actions/soundsConsts';

import {objKeysToArr} from '../Helpers'

class EffectContainer extends Component{
  constructor(props){
    super(props);

    this.changeStyle = this.changeStyle.bind(this);
    this.changeVolume = this.changeVolume.bind(this);
    this.loopSounds = this.loopSounds.bind(this);
    this.fadeSounds = this.fadeSounds.bind(this);
    this.onDragEnd = this.onDragEnd.bind(this);
  }

  onDragEnd(result){
    const {draggableId, source, destination} = result;

    console.log(result);

    if(source.droppableId === destination.droppableId){
      this.props.moveSoundInEffect(destination.droppableId,source.index,destination.index);
    }
    else{
      this.props.moveSoundToEffect(source.droppableId,destination.droppableId,draggableId,destination.index);
    }
  }

  //Changes the effect's playback style to the selected option
  changeStyle(id,value){this.props.changeVar(id,"style",value);}
  //Changes the master volume to the selected value
  changeVolume(id,value){this.props.changeVar(id, 'volume', value);}
  //Sets all sounds to loop
  loopSounds(id){this.props.loopAllSounds(this.props.effects[id].sounds);}
  //Sets all sounds to fade
  fadeSounds(id){this.props.fadeAllSounds(this.props.effects[id].sounds);}

  render(){
    const {collection} = this.props;
    //An array of <effect> elements is constructed
    const effects = objKeysToArr(this.props.effects).map((effect, i) => {
      //Functions are defined to be embedded in a copy of the effect object.
      //This compounded object is passed as a prop into the <effect> object
      const remove = {remove:()=>this.props.removeEffect(effect.id)};
      const cleanUp = {cleanUp:()=>this.props.cleanUp(effect.id,collection.id,effect.sounds)};
      const changeVolume = {changeVolume:(value)=>this.changeVolume(effect.id,value)};
      const changeStyle = {changeStyle:(value)=>this.changeStyle(effect.id,value)};
      const changeVar = {changeVar:(varName,value)=>this.props.changeVar(effect.id,varName,value)};
      const addSounds = {addSounds:(fileArr)=>this.props.createSounds(effect.id,fileArr)};
      const loopAll = {loopAll:()=>this.loopSounds(effect.id)};
      const fadeAll = {fadeAll:()=>this.fadeSounds(effect.id)};
      const addSoundPlaying = {addSoundPlaying:collection.addSoundPlaying};
      const removeSoundPlaying = {removeSoundPlaying:collection.removeSoundPlaying};
      const moveSound = {moveSound:(soundId,oldEffectId)=>this.props.moveSoundToEffect(oldEffectId,effect.id,soundId,0)};

      // A new object is constructed, combining the effect object and the extra functions
      const e = Object.assign({},effect,remove, cleanUp,changeVolume, changeVar, changeStyle, addSounds,loopAll,fadeAll, addSoundPlaying, removeSoundPlaying, moveSound);

      return (
        <div className="col-12 col-lg-6 col-xl-3" key={effect.id}>
          <Effect
            key={effect.id}
            effect={e}
            mediaButtonRef={this.props.mediaButtonRef}
          />
        </div>
      );
    });

    const contentArea = () => {
      if(this.props.app.loading){
        return(
          <div className="row justify-content-center">
            <div className="col-4 text-center dark">
              <h2 className="my-5">Loading...</h2>
            </div>
          </div>
        );
      }
      return(
        <div className="row">
          {effects}
          <div className="col-12 col-lg-6 col-xl-3">
            <div className="effect-card effect-card-add" onClick={()=>this.props.createEffect(this.props.collection.id)}>
              <p><i className="fas fa-plus"></i>Add A New Sound Set</p>
            </div>
          </div>
        </div>
      );
    }

    return(
        contentArea()
    )
  }
}

const mapStateToProps = (state, ownProps) =>{
  let effects = {};
  const collection = ownProps.collection;

  if(collection.effects){
    collection.effects.forEach((e)=>{
      if(typeof state.effects[e] !== 'undefined'){
        Object.assign(effects,{[e]:state.effects[e]})
      }
    });
  }
  return{
    app: state.app,
    effects: effects,
    sounds: state.sounds
  }
}
const mapDispatchToProps = (dispatch) =>{
  return{
    createEffect: (collectionId) =>{
      dispatch((dispatch)=>{
        const effect = effectBuilder("SET")
        dispatch(createEffect(effect));
        dispatch(addEffectToCollection(effect.id,collectionId))
      });
    },
    removeEffect: (effectId) =>{
      dispatch(removeEffect(effectId));
    },
    cleanUp: (effectId, collectionId, sounds) =>{
      dispatch((dispatch)=>{
        dispatch(removeSounds(sounds))
        dispatch(removeEffectFromCollection(effectId, collectionId))
      });
    },
    changeVar: (id, varName, value, extras)=>{
      dispatch(changeEffectVar(id,varName,value));
    },
    createSounds:(effectId, fileArr)=>{
      dispatch(function(dispatch){
        fileArr.forEach((file)=>{
          const sound = soundBuilder(effectId, file.file, file.fileUrl);
          dispatch(createSound(sound));
          dispatch(addSoundToEffect(sound.id, effectId));
        });
      });
    },
    loopAllSounds: (ids) =>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,"loop",true));
        });
      });
    },
    fadeAllSounds: (ids) =>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,"fading",true));
        });
      });
    },
    moveSoundInEffect: (effectId,startIndex,endIndex) =>{
      dispatch(moveSound(effectId,startIndex,endIndex));
    },
    moveSoundToEffect: (oldEffectId, newEffectId, soundId, index) =>{
      dispatch((dispatch)=>{
        dispatch(removeSoundReference(oldEffectId,soundId));
        dispatch(addSoundReference(newEffectId,soundId, index));
        dispatch(changeSoundVar(soundId,"effectId",newEffectId));
      });
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(EffectContainer);



