import JSZip from 'jszip';
import FileSaver from 'file-saver';
import {objKeysToArr, objKeyArray, getFileExtension, formatAudioObj} from '../Helpers'

import {collectionBuilder} from '../actions/collectionsConsts';

//Master function for loading data, split into 3 child functions
export function loadData(e, callbacks){
  let collections, effects, sounds, app = {};
  let meta = null;

  const file = e.target.files[0];
  if(typeof file === 'undefined'){return;}

  getMeta();

  //Loads the meta file from the archive and opens the zip file.
  function getMeta(){
    //Load the file
    JSZip.loadAsync(file).then((zip)=>{
      try{
        //Find meta.json inside the file. If no meta.json, catch the error
        zip.file('meta.json').async('string').then((content) =>{
          //Extract the json as an obj
          meta = JSON.parse(content);
          console.log(meta);
          //Populate variables with the key values of the
          collections = varifyObject(meta.collections)
          effects = varifyObject(meta.effects);
          sounds = varifyObject(meta.sounds);
          app = varifyObject(meta.app);

          if(Object.keys(collections).length === 0){
            let collection = collectionBuilder(app.name);
            let effectIds = [];
            for(var e in effects){
              effectIds.push(e);
            }
            collection.effects = effectIds;
            collections[collection.id] = collection;
          }
          //Call the loading callback
          callbacks.onLoad();

          //Next stage
          getAudio(zip);
        });
      }
      //If the file is an archive, but does not contain a meta.json
      catch(e){
        alert("No meta.json file was found in the archive");
      }
    })
    //If the file isn't an archive
    .catch((error)=>{
      alert("Please use a .immerse file");
    });
  }

  //Extracts the audio and varifies it
  function getAudio(zip){
    let audio = {};
    let fileTotal = Object.keys(zip.files).length;
    let fileCount = 0;

    //Loops through the zip file
    zip.forEach((path,entry)=>{
      if(entry.dir){
        fileCount++;
        return;
      }
      zip.file(path).async('blob').then(function success(content){
        fileCount++;
        if(entry.name === 'meta.json'){return;}
        Object.assign(audio,formatAudioObj(content, entry.name, audio));
        if(fileTotal === fileCount){
          finish(audio);
        }
      }, function error(e){
        alert(e);
        callbacks.onFailure();
      });
    });
  }

  function finish(audio){
    const audioArr = objKeyArray(audio);
    console.log(audio);
    const blob = window.URL || window.webkitURL;
    console.log(sounds);
    console.log(audioArr);
    audioArr.forEach((s) =>{
      sounds[s].url = blob.createObjectURL(audio[s]);
      sounds[s].file = audio[s];
      sounds[s].playing = false;
    });
    const state = {collections,effects,sounds,app};
    callbacks.onSuccess(state);
  }
}


//Exports the state as an archive with added meta.json file
export function exportData(state){
  const str = JSON.stringify(state);
  const file = new File([str], 'meta.json', {type: "text/plain;charset=utf-8"});
  const sounds = Object.assign({},state.sounds);
  let soundArr = objKeysToArr(sounds);
  const effects = state.effects;
  const app = state.app;
  const name = app.name + ".immerse"

  let effectSounds = {};
  for(var key in effects){
    effects[key].sounds.forEach((id)=>{
      effectSounds[id] = true;
    });
  }
  for(let i = soundArr.length - 1; i >= 0; i--){
    if(typeof effectSounds[soundArr[i].id] === 'undefined'){
      soundArr.splice(i,1);
    }
  }

  let zip = new JSZip();

  zip.file("meta.json", file);

  soundArr.forEach((sound)=>{
    zip.folder("sounds")
    .file(sound.id + '.' + getFileExtension(sound.file.name).toLowerCase(), sound.file)
  });

  zip.generateAsync({
    type: "blob",
    compression: "DEFLATE",
    compressionOptions: {
      level: 9
    }
  }).then((content)=>{
    FileSaver.saveAs(content,name);
  });
}

//Simple helper function for varifying if a variable is an object. Returns an object if it is not.
const varifyObject = (item) =>{
  if(typeof item === 'object'){
    return item;
  }
  return {};
}



