import React, {Component} from 'react';
import {connect} from 'react-redux';

import {removeSoundReference} from '../actions/effectsActions';

import Sound from '../components/sound';
import {replaceSound, removeSound, changeSoundVar} from '../actions/soundsActions';

import {changeEffectVar} from '../actions/effectsActions';

import {EffectStyles} from '../actions/effectsConsts';

import {simulateClick, getFileExtension} from '../Helpers';
import {volumeSliderStyle} from '../js/input';

import SoundContainerDropTarget from '../components/dragdrop/SoundContainerDropTarget';
import SoundDragTarget from '../components/dragdrop/SoundDragTarget';
import DragTargets from '../components/dragdrop/DragTargets';

const blob = window.URL || window.webkitURL;

class SoundsContainer extends Component{
  constructor(props){
    super(props);

    this.state = {
      fadeTimer : {},
      delayTimer: null,
      playerRefs : {}
    }

    //File Processing and Changes
    this.newSound = this.newSound.bind(this);
    this.newSoundFromServer = this.newSoundFromServer.bind(this);
    this.replaceSound = this.replaceSound.bind(this);
    this.replaceSoundChangeListener = this.replaceSoundChangeListener.bind(this);
    this.onDrop = this.onDrop.bind(this);

    //Playing
    this.playSound = this.playSound.bind(this);
    this.playAllSounds = this.playAllSounds.bind(this);
    this.playRandomSound = this.playRandomSound.bind(this);
    this.playDelayedSound = this.playDelayedSound.bind(this);
    this.playNextSound = this.playNextSound.bind(this);

    //Pausing
    this.pauseSound = this.pauseSound.bind(this);
    this.pauseAllSounds = this.pauseAllSounds.bind(this);

    //Fading
    this.fadeIn = this.fadeIn.bind(this);
    this.fadeOut = this.fadeOut.bind(this);

    //Stopping
    this.stopSound = this.stopSound.bind(this);
    this.stopAllSounds = this.stopAllSounds.bind(this);
    this.stopOtherSounds = this.stopOtherSounds.bind(this);
    this.stopSoundPlayer = this.stopSoundPlayer.bind(this);

    //Changes
    this.adjustVolume = this.adjustVolume.bind(this);
    this.soloToggle = this.soloToggle.bind(this);

    //Triggers and Listeners
    this.onSoundLoad = this.onSoundLoad.bind(this);
    this.onFinishPlaying = this.onFinishPlaying.bind(this);
    this.getPlayerRefFromChild = this.getPlayerRefFromChild.bind(this);

    //onClick
    this.playButtonClick = this.playButtonClick.bind(this);
    this.pauseButtonClick = this.pauseButtonClick.bind(this);
    this.stopButtonClick = this.stopButtonClick.bind(this);
    this.forwardButtonClick = this.forwardButtonClick.bind(this);
    this.loopButtonClick = this.loopButtonClick.bind(this);
  }

  componentDidMount(){
    this.props.setRef(()=>{this.playButtonClick();},()=>{this.pauseButtonClick()});
  }

  componentWillUnmount(){
    const {delayTimer, fadeTimer} = this.state;
    if(delayTimer !== null){
      clearTimeout(delayTimer);
    }
    if(Object.keys(fadeTimer).length){
      for(var key in fadeTimer){
        clearInterval(fadeTimer[key]);
      }
    }
  }

  //////////////////////////////////
  // File Processing and Changes //
  ////////////////////////////////

  //Adds a new sound file.
  newSound(e) {
    //Checks if a file was provided, if not, it returns
    if(!e.target.files.length) {return;}
    const files = e.target.files;
    const fileArr = [];

    //Processes the provided file
    Array.from(files).forEach((file)=>{
      // Checks if file is of the allowed file type, if not, the file is not pushed to fileArr.
      if(this.processFile(file)){
        fileArr.push({file, fileUrl: blob.createObjectURL(file)});
      }
    });

    //If fileArr contains nothing, program does not bother updating the store and returns.
    if(!fileArr.length){return;}

    //Adds the files to the file store.
    this.props.effect.addSounds(fileArr);
  }

  newSoundFromServer(_file){
    const path = "sounds/" + _file.path;
    const name = _file.name;
    const ext = getFileExtension(_file.path);
    console.log(path);
    console.log(name);
    let blob = null;
    let xhr = new XMLHttpRequest();
    xhr.open("GET",path);
    xhr.responseType = "blob";
    xhr.onload = () =>{
      blob = xhr.response;
      let file = new File([blob],name+"."+ext);
      console.log(file);
      let e = {};
      e["target"] = {};
      e["target"]["files"] = [file];
      this.newSound(e);
    }
    xhr.send();
  }
  //Processes the provided file to determine if it meets certain criteria
  processFile(file){
    // Redundancy check, if the file is undefined than the function returns
    if (typeof file === 'undefined') {return;}

    //File extension is obtained
    const type = getFileExtension(file.name).toLowerCase();
    console.log(type);
    //File extension is compaired to an array of allowed extensions, false is returned if it does not match.
    if (!(['wav', 'mp3', 'ogg', 'm4a'].indexOf(type) > -1)) {return false;}

    //True is returned if there is a match
    return true;
  }
  //Replaces the selected sound with an uploaded file
  replaceSound(soundId){
    this.uploadReplace.removeEventListener("change", this.replaceSoundChangeListener);
    this.uploadReplace.addEventListener("change",this.replaceSoundChangeListener);
    this.uploadReplace.soundId = soundId;
    simulateClick(this.uploadReplace);
  }
  //Listener for replacing a sound
  replaceSoundChangeListener(e){
    //Checks if a file was provided, if not, it returns
    if(e.target.files.length === 0) {return;}
    const file = e.target.files[0];

    // Checks if file is of the allowed file type
    if(this.processFile(file)){
      const fileURL = blob.createObjectURL(file);
      this.props.replaceSound(e.target.soundId, file, fileURL);
    }
  }

  onDrop(file){

    if(typeof file === 'undefined' || typeof file.type === 'undefined'){return};
    switch(file.type){
      case DragTargets.LIBRARY_DRAG_TARGET:
        this.newSoundFromServer(file);
        break;
      case DragTargets.SOUND_DRAG_TARGET:{
        this.props.effect.moveSound(file.soundId,file.effectId);
        break;
      }
    }
  }
  //////////////
  // Playing //
  ////////////

  //Plays the sound that has the provided ID
  playSound(id){
    if(this.props.effect.sounds.length === 0){return}
    this.props.sounds[id].fading ? this.fadeIn(id) : this.props.playSound(id);
    this.props.effect.addSoundPlaying(id);
  }
  //Plays every sound in the effect, or plays all sounds that have already been loaded.
  playAllSounds(){
    let sounds = this.props.effect.sounds.filter((id)=>{
      if(this.props.sounds[id].loaded){ return true;}
      return false;
    });

    if(!sounds.length){ sounds = this.props.effect.sounds;}

    sounds.forEach((id)=>{
      this.playSound(id);
    });
  }
  //Picks a random sound ID and plays that.
  playRandomSound(prevId = null){
    const sounds = this.props.effect.sounds.filter((id)=>{
      if(this.props.sounds[id].playing || id === prevId) {return false;}
      return true;
    });

    if(!sounds.length){return;}

    const id = sounds[Math.floor(Math.random() * sounds.length)];
    this.playSound(id);
  }
  //Runs if the effect is marked for delayed playback.
  playDelayedSound(prevId, finishFunction){
    //If a delay timer has not already been set a timeout is created with a random time
    if(this.state.delayTimer === null){
      const {effect} = this.props;
      const randomTime = Math.floor(((Math.random() * (effect.delayMax - effect.delayMin)) + effect.delayMin) * 1000);
      const delayTimer = setTimeout(()=>{
        //On timeout the function passed in as a parameter is run
        finishFunction(prevId);
        //The delay timer is reset to null
        this.setState({delayTimer:null});
      },randomTime);
      //The delay timer state is set to the delayTimer variable.
      this.setState({delayTimer:delayTimer});
    }
  }
  //Plays the next sound in the order
  playNextSound(prevId = 0){
    const {sounds} = this.props.effect;
    const {playerRefs} = this.state;
    const {changeVar} = this.props;

    //Function will find the id of the sound to play
    //as well as the id of the next sound to play
    let nextId;
    let futureId;

    //if no prevId is provided, than this function has been called from the primary play button, and not from a player finish callback
    if(!prevId){
      //A list of every currently loaded sound is created
      const loadedSounds = sounds.filter((id)=>{
        return this.props.sounds[id].loaded ? true : false;
      });
      //Of any sound is already loaded, that sound is chosen to be played
      if(loadedSounds.length){
        nextId = loadedSounds[0];
        let index = -1;
        sounds.forEach((id, i)=>{
          if(id === nextId){index = i;}
        });
        futureId = sounds[(index + 1) % sounds.length];
      }
      //If not sound is loaded, the first sound in the list is chosen
      else {
        nextId = sounds[0];
        futureId = sounds[1 % sounds.length];
      }
    }
    //If prevId is provided, the previous sound is already known, so finding the next and future sound is simple
    else {
      let index = -1;
      sounds.forEach((id, i)=>{
        if(id === prevId){index = i;}
      });
      nextId = sounds[(index + 1) % sounds.length];
      futureId = sounds[(index + 2) % sounds.length];

    }

    //The future sound is pre-loaded for faster playback, and the next sound is started.
    playerRefs[futureId].load();
    changeVar(futureId,'loaded',true);
    this.playSound(nextId);
  }

  //////////////
  // Pausing //
  ////////////

  //Pause the sound with provided ID
  pauseSound(id){
    this.props.sounds[id].fading ? this.fadeOut(id) : this.props.pauseSound(id);
    this.props.effect.removeSoundPlaying(id);
  }

  //Pause all playing sounds
  pauseAllSounds(){
    const sounds = this.props.effect.sounds.filter((id)=>{
      if (this.props.sounds[id].playing) {return true};
      return false;
    });

    sounds.forEach((id)=>{
      this.pauseSound(id);
    });
  }

  /////////////
  // Fading //
  ///////////

  fadeIn(id, speed = 1000){
    this.props.changeVar(id,'fade',0);
    this.props.playSound(id);
    const change = Math.abs(100 / speed);

    if(typeof this.state.fadeTimer[id] !== 'undefined'){
      clearInterval(this.state.fadeTimer[id]);
    }

    const timer = setInterval(()=>{
      let fade = this.props.sounds[id].fade + change;

      if(fade >= 1 || !this.props.sounds[id].playing){
        this.props.changeVar(id,'fade',1);
        clearInterval(timer);
      }
      else {this.props.changeVar(id,'fade',fade);}
    },100);

    const fadeTimerObj = {[id]:timer};
    this.setState((prevState)=>({
      fadeTimer: Object.assign({},prevState.fadeTimer,fadeTimerObj)
    }));
  }

  fadeOut(id, speed = 1000){
    this.props.changeVar(id,'fade',1);
    const change = Math.abs(100 / speed);

    if(typeof this.state.fadeTimer[id] !== 'undefined'){
      clearInterval(this.state.fadeTimer[id]);
    }

    const timer = setInterval(()=>{
      let fade = this.props.sounds[id].fade - change;
      if(fade <= 0 || !this.props.sounds[id].playing){
        clearInterval(timer);
        this.props.pauseSound(id);
        this.props.changeVar(id,'fade',1);
      }
      else {this.props.changeVar(id,'fade',fade);}
    },100)

    const fadeTimerObj = {[id]:timer};
    this.setState((prevState)=>({
      fadeTimer: Object.assign({},prevState.fadeTimer,fadeTimerObj)
    }));
  }

  ///////////////
  // Stopping //
  /////////////

  //Stops the sounds with provided ID, kills the player, and unloads the file.
  stopSound(id, callback = function(){}){
    this.stopSoundPlayer(id);
    this.props.stopSound(id);
    this.props.effect.removeSoundPlaying(id, callback);
  }

  //Stops all sounds
  stopAllSounds(){
    console.log("Stop All Sounds");
    const sounds = this.props.effect.sounds.filter((id)=>{
      const sound = this.props.sounds[id];
      return sound.playing || sound.loaded ? true : false;
    });
    sounds.forEach((id)=>{
      this.stopSound(id);
    });
  }

  //Stops all sounds besides the provided ID
  stopOtherSounds(id){
    const sounds = this.props.effect.sounds.filter((s) => s !== id);
    sounds.forEach((id)=>{
      this.stopSound(id);
    });
  }

  //Kills and unloads the sound's player via ref.
  stopSoundPlayer(id){
    const player = this.state.playerRefs[id];
    player.stop();
    player.howler.unload();
  }

  //////////////
  // Changes //
  ////////////

  //Changes the volume of the sound
  adjustVolume(soundId,value){
    this.props.changeVar(soundId, 'volume', value);
  }
  //Toggles the solo variable true on the provided id, and toggles solo to false on all others.
  soloToggle(id){
    const ids = this.props.effect.sounds.filter((s)=> s !== id);
    const solo = !this.props.sounds[id].solo;
    this.props.soloToggle(id,ids,solo);
  }

  ////////////////////////////
  // Trigger and Listeners //
  //////////////////////////

  //Triggers when sound is done loading
  onSoundLoad(id){
    this.props.changeVar(id, 'loaded', true);
  }
  //Triggers when sound finished playing, but only if the sound is not set to loop.
  onFinishPlaying(id, player){
    const loop = this.props.sounds[id].loop;
    if(!loop){
      this.stopSound(id);
    }
    switch(this.props.effect.style){
      case EffectStyles.DEFAULT:
        break;
      case EffectStyles.RANDOM:
        if(this.props.effect.randomLoop){
          this.props.effect.delay ? this.playDelayedSound(id,(id)=>{
            this.playRandomSound(id);
          }) : this.playRandomSound(id);
        }
        break;
      case EffectStyles.SEQUENTIAL:
        if(!loop){
          this.props.effect.delay ? this.playDelayedSound(id,(id)=>{
            this.playNextSound(id);
          }) : this.playNextSound(id);
        }
        break;
      default:
        break;
    }
  }

  getPlayerRefFromChild(id,player){
    console.log(player);
    const playerRef = {[id]:player}
    this.setState(prevState =>({
      playerRefs: Object.assign(playerRef, prevState.playerRefs)
    }));
  }

  killDelayTimer(){
    if(this.state.delayTimer !== null){
      clearTimeout(this.state.delayTimer);
      this.setState({delayTimer:null});
    }
  }

  ///////////////
  // On Click //
  /////////////

  playButtonClick(){
    if(!this.props.effect.sounds.length){return;}
    switch(this.props.effect.style){
      case EffectStyles.DEFAULT:
        this.playAllSounds();
        break;
      case EffectStyles.RANDOM:
      this.props.effect.delay ? this.playDelayedSound(null,(id)=>{
        this.playRandomSound(id);
      }) : this.playRandomSound();
        break;
      case EffectStyles.SEQUENTIAL:
        this.playNextSound();
        break;
      default:
        break;
    }
  }

  pauseButtonClick(){
    switch(this.props.effect.style){
      case EffectStyles.DEFAULT:
        this.pauseAllSounds();
        break;
      case EffectStyles.RANDOM:
        this.killDelayTimer()
        this.stopAllSounds();
        break;
      case EffectStyles.SEQUENTIAL:
        this.pauseAllSounds();
        break;
      default:
        break;
    }
  }

  stopButtonClick(){
    this.stopAllSounds();
  }

  forwardButtonClick(){
    const sounds = this.props.effect.sounds.filter((id)=>{
      return this.props.sounds[id].playing ? true : false;
    });

    sounds.forEach((id)=>{
      this.stopSound(id,()=>{
        this.playNextSound(id);
      });
    });
  }

  loopButtonClick(){
    this.props.changeEffectVar(this.props.effect.id,"randomLoop",!this.props.effect.randomLoop);
  }

  render(){
    const soundsArr = [];
    const {removeSound, changeVar, effect, sounds, app, changeEffectVar} = this.props;

    //Advanced styles for the volume slider
    const volumeStyles = () =>{
     return volumeSliderStyle(this.props.effect.volume)
    }

    //Volume icon dependent on loudness
    const volumeIcon = () =>{
      if(effect.mute){return "fas fa-volume-mute icon muted";}
      if(effect.volume > 50){return "fas fa-volume-up icon";}
      else{return "fas fa-volume-down icon";}
    }

    //Pause, Play controls dependent on factors
    const mediaButtons = () =>{
      const PlayButton = <a className="effect-media-button" onClick={this.playButtonClick} key="play"><i className="fas fa-play"></i></a>;
      const PauseButton = <a className="effect-media-button active" onClick={this.pauseButtonClick} key="pause"><i className="fas fa-pause"></i></a>;
      const StopButton = <a className="effect-media-button-small" onClick={this.stopButtonClick} key="stop"><i className="fas fa-stop"></i></a>;
      const LoopButton = <a className="effect-media-button-small" onClick={this.loopButtonClick} key="loop"><i className="fas fa-undo"></i></a>;
      const LoopButtonActive = <a className="effect-media-button-small active" onClick={this.loopButtonClick} key="loop"><i className="fas fa-undo"></i></a>;

      const ForwardButton = <a className="effect-media-button-small" onClick={this.forwardButtonClick} key="forward"><i className="fas fa-step-forward"></i></a>;
      const sound = this.props.effect.sounds.filter((s)=>this.props.sounds[s].playing);
      let buttons = [];

      if(sound.length || this.state.delayTimer !== null){
        buttons.push(PauseButton);
      }else{
        buttons.push(PlayButton);
      }

      switch(effect.style){
        case EffectStyles.SEQUENTIAL:
          buttons.push(ForwardButton);
          break;
        case EffectStyles.RANDOM:
          if(effect.randomLoop){
            buttons.push(LoopButtonActive);
          }else{
            buttons.push(LoopButton);
          }
          break;
        default:
          break;
      }

      buttons.push(StopButton);
      return buttons;
    }

    const volume = () =>{
      return(
      <div className="volume">
        <i className={volumeIcon()} onClick={()=>changeEffectVar(effect.id,'mute',!effect.mute)}></i>
        <input className="volume-input" type="range"  min="0" max="100" value={effect.volume} onChange={(e)=>effect.changeVolume(e.target.value)}/>
        <div className="volume-track" style={volumeStyles()}></div>
      </div>);
    }

    const extraControls =()=>{
      const Delay = buildDelayOption();

      return(<div className="effect-extra-controls">
        <ul className="no-style">
          {Delay}
        </ul>
      </div>);
    }

    const buildDelayOption = () =>{
      if(effect.style === EffectStyles.DEFAULT){return}

      const step = 1;
      const min = effect.delayMin;
      const max = effect.delayMax;
      const minMax = round(max - step);
      const maxMin = round(parseFloat(min) + step);
      const minChange = (e)=>effect.changeVar("delayMin",round(e.target.value));
      const maxChange = (e)=>effect.changeVar("delayMax",round(e.target.value));

      function round(value){
        return (Math.round(value * 10) / 10);
      }

      return (
        <li className={effect.delay ? "delay":"delay inactive"}>
          <p onClick={()=>effect.changeVar('delay',!effect.delay)}>Delay</p>
          <div className="wrapper inactive-response">
            <p>MIN</p>
            <input type="number" name="" maxlength="3" disabled={(effect.delay)? "" : "disabled"}  value={min} step={step} min={step} max={minMax} onChange={minChange} />
            <p>MAX</p>
            <input type="number" name="" maxlength="3" disabled={(effect.delay)? "" : "disabled"}  value={max} step={step} min={maxMin} max={100} onChange={maxChange}/>
          </div>
        </li>
      );
    }

    effect.sounds.forEach((id, i)=>{
      //Additional functions embedded inside of the normal sound obj to be passed into the sound component.
      //This method simplifies a large portion of the code inside both the container and the component.
      const play = {play:()=>this.playSound(id)};
      const pause = {pause:()=>this.pauseSound(id)};
      const stop = {stop:()=>this.stopSound(id)};
      const remove = {remove:()=>removeSound(effect.id,id)};
      const replace = {replace:()=>this.replaceSound(id)};
      const adjustVolume = {adjustVolume:(value)=>this.adjustVolume(id,value)};
      const soloToggle = {soloToggle:()=>this.soloToggle(id)};
      const changeName = {changeName:(value) => changeVar(id, 'name', value)};
      const onLoad = {onLoad:()=>this.onSoundLoad(id)};
      const onFinish = {onFinish:(player)=>this.onFinishPlaying(id,player)};
      const loopToggle = {loopToggle:()=>changeVar(id,'loop',!sounds[id].loop)};
      const muteToggle = {muteToggle:()=>changeVar(id,'mute',!sounds[id].mute)};
      const fadeToggle = {fadeToggle:()=>changeVar(id,'fading',!sounds[id].fading)};

      //The final obj constructed by combining the above objs with the primary sound obj.
      const sound = Object.assign({},sounds[id],play,pause,stop,remove,replace,adjustVolume,soloToggle,changeName,onLoad,onFinish,loopToggle,muteToggle,fadeToggle);

      soundsArr.push(
        <SoundDragTarget key={sound.id} soundId={sound.id} effectId={effect.id} classes={sound.playing ? "sound-card playing" : "sound-card"}>
          <Sound sound={sound} effect={effect} app={app} passUpPlayerRef={(player)=>this.getPlayerRefFromChild(sound.id,player)}/>
        </SoundDragTarget>
      )
    });

    return(
      <div>
        <div className="effect-controls">
          <div className="media-buttons">
            {mediaButtons()}
          </div>
            {volume()}
        </div>
        <div className={effect.minimized ? "d-none":""}>
          {extraControls()}
          <SoundContainerDropTarget onDrop={this.onDrop}>
            <div className="sound-container">
              {soundsArr}
              <input type="file" style={{display: "none"}} onChange={this.newSound} ref={(ref)=>(this.uploadNew = ref)} multiple/>
              <div className="sound-card new-sound" onClick={(e) => simulateClick(this.uploadNew)}>
                <i className="fas fa-arrow-circle-up"></i>
                <p>Add New Sound</p>
              </div>
            </div>
          </SoundContainerDropTarget>
          <input type="file" style={{display: "none"}} ref={(ref) =>(this.uploadReplace = ref)}/>
        </div>
      </div>

    );
  }
}

const mapStateToProps = (state, ownProps) =>{
  let sounds = {};
  const effect = ownProps.effect;
  //If the effect contains any sound id, that id is used to obtain the actual sound variable from the sounds state
  if(effect.sounds){
    effect.sounds.forEach((s)=>{
      if(typeof state.sounds[s] !== 'undefined'){
        Object.assign(sounds,{[s]:state.sounds[s]})
      }
    });
  }

  return{
    sounds: sounds,
    app: state.app
  };
}

const mapDispatchToProps = (dispatch) =>{
  return{
    playSound: (id)=>{
      dispatch(dispatch=>{
        dispatch(changeSoundVar(id,"playing",true));
        dispatch(changeSoundVar(id,"loaded",true));
      });
    },
    playSounds: (ids)=>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,'playing',true));
        });
      });
    },
    pauseSound: (id)=>{
      dispatch(changeSoundVar(id,"playing",false));
    },
    pauseSounds: (ids)=>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,'playing',false));
        });
      });
    },
    stopSound: (id)=>{
      dispatch(dispatch=>{
        dispatch(changeSoundVar(id,"playing",false));
        dispatch(changeSoundVar(id,"loaded",false));
      });
    },
    stopSounds: (ids)=>{
      dispatch((dispatch)=>{
        ids.forEach((id)=>{
          dispatch(changeSoundVar(id,'playing',false));
          dispatch(changeSoundVar(id,'loaded',false));
        });
      });
    },
    soloToggle: (id,ids,solo)=>{
      dispatch((dispatch)=>{
        if(solo){
          dispatch(changeSoundVar(id,'solo',true));
          dispatch(changeSoundVar(id,'soloMute',false));
          ids.forEach((id)=>{
            dispatch(changeSoundVar(id,'solo',false));
            dispatch(changeSoundVar(id,'soloMute',true));
          });
        }
        else{
          dispatch(changeSoundVar(id,'solo',false));
          ids.forEach((id)=>{
            dispatch(changeSoundVar(id,'soloMute',false));
          });
        }
      });
    },
    replaceSound: (id, file, fileUrl) =>{
      dispatch(replaceSound(id, file,fileUrl))
    },
    removeSound: (effectId,soundId) =>{
      dispatch(function(dispatch){
        dispatch(removeSoundReference(effectId,soundId));
        dispatch(removeSound(soundId));
      });
    },
    changeVar: (id, varName, value, extras)=>{
      dispatch(changeSoundVar(id,varName,value,extras));
    },
    changeEffectVar:(id, varName, value)=>{
      dispatch(changeEffectVar(id,varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(SoundsContainer);



