import React, {Component} from 'react';
import {connect} from 'react-redux';

import {simulateClick, objKeysToArr} from '../Helpers'
import {loadCollections, unloadCollections} from '../actions/collectionsActions';
import {loadEffects, unloadEffects} from '../actions/effectsActions';
import {loadSounds, unloadSounds} from '../actions/soundsActions';
import {loadApp,changeAppVar} from '../actions/appActions';

import {loadData, exportData} from '../js/file_manager';

class ControlsContainer extends Component{
  constructor(props){
    super(props);

    this.load = this.load.bind(this);
    this.export = this.export.bind(this);
    this.playAllClicked = this.playAllClicked.bind(this);
    this.pauseAllClicked = this.pauseAllClicked.bind(this);
  }

  load(e){
    loadData(e,{
      onLoad: () =>{
        this.props.changeAppVar('loading',true);
      },
      onFailure: ()=>{
        this.props.changeAppVar('loading',false);
      },
      onSuccess: (state) =>{
        this.props.unload();
        this.props.load(state.collections,state.effects,state.sounds,state.app);
      }
    });
  }
  export(){
    exportData(this.props.state);
  }

  playAllClicked(){
    const arr = objKeysToArr(this.props.mediaButtons);
    arr.forEach((i)=>{
      i.play();
    });
  }
  pauseAllClicked(){
    const arr = objKeysToArr(this.props.mediaButtons);
    arr.forEach((i)=>{
      i.pause();
    });
  }

  render(){
    return(
      <div className="sidebar sidebar-main dark ">
        <div className="sidebar-head">
          <h5>Sound Mixer</h5>
        </div>
        <div className="sidebar-content no-padding">
          <ul className="no-style">
            <li>
              <span>App</span>
            </li>
            <li>
              <button type="button" name="button" onClick={(e) => simulateClick(this.loadInput)}>
                <i className="fas fa-upload"></i>
                Load
              </button>
              <input type='file' style={{display:"none"}} onChange={this.load} ref={(ref) => {this.loadInput = ref}}></input>
            </li>
            <li>
              <button type="button" name="button" onClick={this.export}>
                <i className="fas fa-download"></i>
                Export
              </button>
            </li>
            {/*
            <li>
              <button type="button" name="button" onClick={this.playAllClicked}>
                <i className="fas fa-play"></i>
                Play All
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={this.pauseAllClicked}>
                <i className="fas fa-pause"></i>
                Pause All
              </button>
            </li>
            */}
            <li>
              <span>Soundscapes</span>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState("LIBRARY")}>
                <i className="fas fa-folder"></i>
                Sound Library
              </button>
            </li>
            <li>
              <span>Info</span>
            </li>
            <li>
              <button type="button" name="button">
                <i className="fas fa-question"></i>
                How To Use
              </button>
            </li>
            <li>
              <button type="button" name="button" onClick={()=>this.props.changeSidebarState("ABOUT")}>
                <i className="fas fa-book-open"></i>
                About
              </button>
            </li>
            <li>
              <span>Donate</span>
            </li>
            <li>
            <button type="button" name="button">
              <i className="fab fa-patreon"></i>
              Patreon
            </button>
            </li>
            <li>
              <button type="button" name="button">
              <i className="fab fa-paypal"></i>
              Paypal
              </button>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    app: state.app,
    sounds: state.sounds,
    state: state
  };
}
const mapDispatchToProps = (dispatch) =>{
  return{
    unload: () =>{
      dispatch(unloadCollections());
      dispatch(unloadEffects());
      dispatch(unloadSounds());
    },
    load: (collections, effects, sounds, app) =>{
      dispatch((dispatch)=>{
        dispatch(loadSounds(sounds));
        dispatch(loadEffects(effects));
        dispatch(loadCollections(collections))
        dispatch(loadApp(app));
        dispatch(changeAppVar("loading",false));
      });
    },
    changeAppVar: (varName, value)=>{
      dispatch(changeAppVar(varName,value));
    }
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(ControlsContainer);



