import React, {Component} from 'react';
import SoundLibraryDragTarget from './dragdrop/SoundLibraryDragTarget';

class SoundLibrary extends Component{
  render(){
    const {sounds, opened, folderClicked} = this.props;

    const fileList = () =>{
      let structure = [];

      const buildHead = () =>{
        const {head, tree} = sounds;

        head.forEach((item)=>{
          structure.push(
            <li key={item} className={typeof opened[item] !== 'undefined' ? "open" : ""}>
              <div className="folder" onClick={()=>folderClicked(item)}>
                <span className="item-name">
                  <i className={typeof opened[item] !== 'undefined' ? "fas fa-folder-open" : "fas fa-folder"}></i>
                  <p>{tree[item].name}</p>
                </span>
                <span className="item-chevron">
                  <i className={typeof opened[item] !== 'undefined' ? "fas fa-angle-down" : "fas fa-angle-right"}></i>
                </span>
              </div>
              <ul className="no-style">
                {typeof opened[item] !== 'undefined' &&
                split(tree[item])}
              </ul>
            </li>
          )
        });
      }

      const buildFolder = (obj) =>{
        return (
          <li key={obj.path} className={typeof opened[obj.path] !== 'undefined' ? "open" : ""}>
            <div className="folder" onClick={()=>folderClicked(obj.path)}>
              <span className="item-name">
                <i className={typeof opened[obj.path] !== 'undefined' ? "fas fa-folder-open" : "fas fa-folder"}></i>
                <p>{obj.name}</p>
              </span>
              <span className="item-chevron">
                <i className={typeof opened[obj.path] !== 'undefined' ? "fas fa-angle-down" : "fas fa-angle-right"}></i>
              </span>
            </div>
            <ul className="no-style">
              {typeof opened[obj.path] !== 'undefined' &&
              split(obj)}
            </ul>
          </li>
        );
      }

      const buildFile = (obj) =>{
        return(
          <SoundLibraryDragTarget file={obj} classes="" key={obj.path}>
            <li onClick={()=>this.props.soundClicked(obj)}>
              <div className="file">
                <span className="item-name">
                  <i className="fas fa-volume-off"></i>
                  <p>{obj.name}</p>
                </span>
              </div>
            </li>
          </SoundLibraryDragTarget>
        );
      }

      buildHead();

      function split(obj){
        const {children} = obj;
        const _sounds = obj.sounds;
        const {tree} = sounds;

        let subStructure = [];

        if(typeof children !== 'undefined' && children.length){
          children.forEach((child)=>{
            subStructure.push(buildFolder(tree[child]));
          });
        }
        if(typeof _sounds !== 'undefined' && _sounds.length){
          _sounds.forEach((sound)=>{
            subStructure.push(buildFile(tree[sound]));
          });
        }
        return subStructure;
      }

      return(
        <ul className="no-style library-list">
        {structure}
        </ul>
      );
    }

    return(
      <div className={"sidebar sidebar-library dark " + this.props.classes}>
      <div className="sidebar-head">
        <button type="button" name="button" className="back" onClick={this.props.sidebarMain}><i className="fas fa-angle-left"></i></button>
        <h5>Sound Library</h5>
      </div>
        <div className="sidebar-content no-padding">
          {fileList()}
        </div>
      </div>
    )
  }
}
export default SoundLibrary;



